@import './root-styles.css';
@import './root-utils.css';
@import './root-typography.css';
@import './utils.css';

/* STATIC STYLES (desktop-first) */

body {
  display: flex;
  flex-direction: column;
  align-items: center;

  min-height: 100vh;

  text-align: center;
}

main {
  flex: 1;
}

section {
  place-content: center;

  max-width: 1200px;
  margin: auto;

  text-align: center;
}

section:not(:first-child) {
  margin: 0 auto;
  padding: 12rem 3rem;
}

.section-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  gap: 1rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  section:not(:first-child) {
    padding: 10rem 3rem;
  }
}

@media (max-width: 992px) {
  section:not(:first-child) {
    padding: 7rem 2rem;
  }
}

@media (max-width: 768px) {
  section:not(:first-child) {
    padding: 5rem 1rem;
  }
}

@media (max-width: 576px) {
  section:not(:first-child) {
    padding: 3rem 1rem;
  }
}
