/* STATIC STYLES (desktop-first) */

.portfolio {
  display: flex;
  flex-direction: column;

  width: 100vw;

  gap: 4rem;
  padding-inline: 3rem;
}

.portfolio-cards {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  margin: auto;

  gap: 3rem;
}

.portfolio-cards p {
  margin-block: 0.7rem;
}

.portfolio-cards > .card3:not(:first-child) {
  margin-top: -10rem;
}

.portfolio-cards > .card3:nth-child(odd) {
  margin-right: calc(50% + 4rem);

  text-align: center;
}

.portfolio-cards > .card3:nth-child(even) {
  margin-left: calc(50% + 4rem);

  text-align: center;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .portfolio-cards > .card3:not(:first-child) {
    margin-top: 1rem;
  }

  .portfolio-cards > .card3:nth-child(odd) {
    margin-right: 0;
  }

  .portfolio-cards > .card3:nth-child(odd), .portfolio-cards > .card3:nth-child(even) {
    margin-left: 0;
  }
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .portfolio-cards > .card3:nth-child(odd), .portfolio-cards > .card3:nth-child(even) {
    margin-left: 0;
  }
}

@media (max-width: 576px) {
}
