/* STATIC STYLES (desktop-first) */

.card3 {
  position: relative;

  max-width: 90vw;
  padding: 1.5rem;

  overflow: hidden;
  border: 3px solid var(--color-primary-dark);
  border-radius: 2rem;

  background: var(--color-primary-darkest);
  backdrop-filter: blur(10px);

  transform-style: preserve-3d;
  transition: 0.6s ease;
}

.card3:hover {
  border-color: var(--color-secondary-dark);

  box-shadow: 0 0 30px var(--color-secondary-dark);

  transform: rotateY(5deg) scale(1.03);
}

.card3::before {
  content: '';
  position: absolute;
  top: 0;
  left: -75%;

  width: 150%;
  height: 100%;

  background: linear-gradient(120deg, transparent, var(--color-primary-dark), transparent);
  opacity: 0;

  transform: skewX(-20deg);
  pointer-events: none;
}

.card3:hover::before {
  animation: smooth-shine 1.8s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

@keyframes smooth-shine {
  0% {
    left: -75%;

    opacity: 0;
  }
  20% {
    opacity: 0.8;
  }
  50% {
    left: 125%;

    opacity: 0.6;
  }
  100% {
    left: 125%;

    opacity: 0;
  }
}

