/* STATIC STYLES (desktop-first) */

header {
  position: fixed;
  top: 0;
  z-index: 40;

  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  margin: 0 auto;
  padding: 1.25rem 2rem;

  background-color: transparent;

  transition: all 0.3s ease;
}

.header-scrolled {
  padding: 1rem 1.75rem;

  background-color: var(--color-blur);
  backdrop-filter: blur(8px);
}

.header-logo {
  color: var(--color-secondary);
}

.desktop-nav {
  display: flex;
}

.mobile-nav {
  display: none;
}

.nav-link {
  margin-left: 2rem;

  text-decoration: none;
  color: var(--color-primary-text-dark);

  transition: color 0.3s;
}

.nav-link:hover {
  color: var(--color-secondary);
}

.menu-button {
  display: none;
}

.mobile-nav.open {
  opacity: 1;

  pointer-events: auto;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .desktop-nav {
    display: none;
  }

  .mobile-nav {
    position: fixed;
    z-index: 40;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 100vh;

    background-color: var(--color-card);
    backdrop-filter: blur(8px);
    opacity: 0;

    inset: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;

    gap: 2rem;
  }

  .menu-button {
    z-index: 50;

    display: block;

    border: none;

    background: none;

    color: var(--color-primary-text);

    cursor: pointer;
  }
}

