/* STATIC STYLES (desktop-first) */

.services {
  display: flex;
  flex-direction: column;

  gap: 5rem;
}

.services-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 2rem;
}

.services-cards > * {
  flex: 0 0 20rem;
}

.services-cards p {
  padding-block: 0.2rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .services {
    padding-inline: 8rem;
  }
}

@media (max-width: 576px) {
  .services {
    padding-inline: 2rem;
  }

  .services-cards {
    flex-direction: column;
  }

  .services-cards > * {
    flex: 1;
  }
}
