/* STATIC STYLES (desktop-first) */

.card2 {
  z-index: 0;

  padding: 2rem;

  border: 3px solid var(--color-primary-dark);
  border-radius: 2rem;

  background: var(--color-primary-darkest);

  text-align: center;

  transition: 0.5s ease-in-out;
}

.card2:hover {
  border: 3px solid var(--color-secondary-dark);

  box-shadow: 0 0 20px var(--color-secondary-dark);

  animation: float 4s ease-in-out infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.card2-header {
  margin-bottom: 1rem;

  line-height: 1.1;
  color: var(--color-secondary);
}

.card2-inner {
  display: inline-block;

  text-align: left;
}

.card2-inner p {
  display: flex;
  align-items: center;

  gap: 0.75rem;
}

